import React, { Component } from "react";
import lottie from "lottie-web";
import styled from "styled-components";
import { Parallax, Background } from "react-parallax";

import SomeJPEG from "../../json/images/perspective_distort.jpg";
import SomeJPEGInvis from "../../json/images/perspective_distort.jpg";
import transparentGif from "../../json/images/transparent.gif";

const Wrapper = styled.div`
  margin: 0 auto;
  width: 80%;
  background-color: white;
  box-shadow: var(--shadow-light);
  border-radius: 5px;
  .lottie-animation {
    position: fixed;
    width: 80%;
    top: 20px;
    left: 50%;
    border: solid 1px black;
    border-radius: 5px;
    margin: 0 auto;
    z-index: 10;
  }
`;

export class LottieScrollTest extends Component {
  state = { frames: 0, animObj: {} };

  componentDidMount() {
    this.anim = lottie.loadAnimation({
      container: this.animBox, //the DOM element which will contain the animation
      renderer: "svg",
      loop: false,
      autoplay: false,
      animationData: this.props.JsonAnimation,
      name: this.props.name,
    });
    this.frames = this.anim.getDuration(true);
    this.setState({ frames: this.frames });
  }

  render() {
    console.log(`this is called from outside ${this.frames} frames`);
    return (
      <Wrapper>
        {/* <div style={{ height: "1000px" }}>Some Text</div> */}
        <div
          className="lottie-animation"
          ref={(ref) => (this.animBox = ref)}
        ></div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <div style={{ height: "200px" }}></div>
        <div>Scroll Down</div>
        <Parallax
          blur={0}
          bgImage={transparentGif}
          bgImageAlt="the cat"
          strength={0}
          renderLayer={(percentage) => (
            <div>
              {console.log(percentage)}
              Hello Moto
              {lottie.goToAndStop(
                Math.round(this.frames * percentage * 0.8),
                true
              )}
            </div>
          )}
        >
          <div></div>

          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
          <div style={{ height: "200px" }}></div>
          <div>Scroll Down</div>
        </Parallax>
      </Wrapper>
    );
  }
}

export default LottieScrollTest;
